.posts {
  width: 1232px;
  margin: 186px 148px 100px;
  padding: 23px 77px 12px;
  background: $bg-light;
  box-shadow: 0px 4px 27px rgba(230, 231, 234, 0.78);
}

.posts__search {
  margin-bottom: 15px;
}

.posts__table {
  margin-bottom: 16px;
}

.posts__loading-message {
  @include controls-button-text;
  margin: 40px auto;
  text-align: center;
  color: $text-color;
}

.posts__error-message {
  @include controls-button-text;
  margin: 40px auto;
  text-align: center;
  color: red;
}
